.page__header {
  padding-top: rem(55);
}
.page__header_white {

}
.page-header {

}
.page-header__body {

}
.page-header__logo {

}
.page-header__logo-img {

}
.page-header__top {
  margin-bottom: rem(31);
}
.page-header__address {
  display: flex;
  font-family: $FiraSans;
  color: #ffffff;
  font-weight: 400;
  .icon__location {
    flex-shrink: 0;
    margin-right: rem(17);
  }
}

.page-header__address-right {

}
.page-header__address-name {

  font-size: 13px;

  line-height: 1.46;
}
.page-header__address-val {
  font-size: 15px;
  line-height: 1.27;
  margin-bottom: 5px;
}
.page-header__address-btn {
  @include btn;
  font-size: 12px;
  font-weight: 600;
  padding: 6px 8px;
}
.page-header__weather {
  display: flex;
  justify-content: flex-end;
  margin-top: rem(-12);
}
.page-header__weather-ico {
  flex-shrink: 0;
  margin-left: rem(25);
  margin-top: rem(5);
  svg {
    fill: white;
    max-width: 51px;
    max-height: 46px;
  }
}
.page-header__weather-left {
  max-width: 150px;
  text-align: right;
}
.page-header__weather-num {
  font-family: $FiraSans;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
  color: #ffffff;
  font-size: rem(38);
  font-weight: 500;
}
.page-header__weather-text {
  font-family: $Montserrat;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
  color: #ffffff;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.08;
}
.page-header__bottom {

}

.page-header__menu {
  display: flex;
  flex-wrap: wrap;
}
.page-header__menu-link {
  display: inline-block;
  flex-grow: 1;
  padding: 5px;
  font-family: $FiraSans;
  color: #ffffff;
  font-size: rem(18);
  font-weight: 500;
  line-height: 1;
  text-align: center;
  @include link-h;
}
.page-header__gamburger {
  display: none;
}
.page-header__logo-col {
  @include make-col-ready();
  @include make-col(2.3)
}
.page-header__main-col {
  @include make-col-ready();
  @include make-col(9.7)
}
.page-header__map {
  height: 500px;
  max-height: 80vh;
}
.page-header__mobile-col {
  display: none;
}
.page-header__call {
  display: none;
}
@include media-breakpoint-down(md) {
  .page-header__gamburger {
    display: inline-block;
    padding: 5px;
    overflow: hidden;
    margin-left: 10px;
  }
  .page-header__logo-col {
    @include make-col(4)
  }
  .page-header__mobile-col {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    @include make-col-ready();
    @include make-col(8)
  }
  .page-header__main-col {
    display: none;
    position: absolute;
    top: 100%;
  }
  .page-header {
    background: #00569c;
    padding: 8px 0;
    position: relative;
  }
  .page-header__main-col {
    background: #00569c;
    flex: 0 0 auto;
    max-width: none;
    left: 0;
    right: 0;
    padding-bottom: 15px;
  }
  .page-header__menu {
    flex-direction: column;
  }
  .page-header__menu-link {
    font-size: 17px;
    padding: 15px;
  }
  .page-header__logo-img {
    max-height: 60px;
  }
  .page-header .row {
    align-items: center;
  }
  .page-header__weather {
    flex-direction: row-reverse;
    margin-top: rem(30);
  }
  .page-header__weather-left {
    text-align: left;
  }
  .page-header__weather-ico {
    margin-right: rem(25);
    margin-left: 0;
  }
  .page-header__address {
    margin-top: rem(30);
  }
  .page-header__call {
    display: inline-flex;
    vertical-align: top;
    align-items: center;
    justify-content: center;

    border: 2px solid #7cb355;
    width: 31px;
    height: 31px;
    border-radius: 50%;
    margin-right: 15px;
    svg {
      fill: #7cb355;
      max-width: 60%;
      max-height: 60%;
    }
  }

}
