.page-footer {
  border-top: 1px solid #e1e1e1;
  padding-top: rem(43);
}
.page-footer__menu {
  margin: 0 -3px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  height: 100%;
  align-items: center;
}
.page-footer__menu-link {
  display: inline-block;
  vertical-align: top;
  font-family: $FiraSans;
  color: #4c4c4c;
  font-size: rem(18);
  font-weight: 500;
  line-height: 1;
  padding: 8px 3px;
  @include link-h;
}
.page-footer__btn-col {
  text-align: right;
}
.page-footer__btn {
  @include btn;
  font-size: 13px;
  font-weight: 900;
  letter-spacing: 1px;
  text-transform: uppercase;
  padding: rem(21) rem(42);
}
.page-footer__logo {
  display: block;
  margin-top: rem(10);
  margin-bottom: rem(12);
}
.page-footer__link {
  font-family: $openSans;
  color: #4e4e4e;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.58;
  text-decoration: underline;
  display: block;
  margin-bottom: 4px;
  @include link-h;
}
.page-footer__top {
  margin-bottom: 39px;
}
.page-footer__column {
  border-left: 1px solid  #e1e1e1;
  padding-left: rem(52);
}
.page-footer__col-left {
  @include make-col-ready();
  @include make-col(2.3);
}
.page-footer__col-center {
  @include make-col-ready();
  @include make-col(4.2);
}
.page-footer__col-right {
  @include make-col-ready();
  @include make-col(5.5);
}
.page-footer__column-title {
  font-family: $FiraSans;
  color: #252525;
  font-size: 17px;
  font-weight: 500;
  line-height: 1.59;
  margin-bottom: rem(14);
}
.page-footer__address {
  font-family: $openSans;
  color: #252525;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
  margin-bottom: rem(19);
}
.page-footer__contact-item {
  font-family: $openSans;
  margin-bottom: rem(8);
}
.page-footer__contact-name {
  color: #252525;
  font-size: 14px;
  font-weight: 400;
  line-height: 0.93;
  display: inline-block;
  vertical-align: top;
  margin-right: 6px;
}
.page-footer__contact-val {
  color: #1a6fc3;
  font-size: 14px;
  font-weight: 400;
  line-height: 0.93;
  vertical-align: top;
  @include link-h;
}
.page-footer__map {
  height: 258px;
  margin-top: rem(16);
}
.page-footer__copyright {
  padding: 23px 0;
}
.page-footer__copy-text {
  font-family: $openSans;
  color: #6b6b6b;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.23;
}
.page-footer__autor {
  font-family: $openSans;
  color: #6b6b6b;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.38;
  text-align: right;
  display: block;
  img {
    margin-left: 10px;
    display: inline-block;
  }
}

@include media-breakpoint-down(md) {
  .page-footer__btn-col {
    text-align: left;
    margin-top: rem(30);
  }
  .page-footer__col-left {
    @include make-col(3);
  }
  .page-footer__col-center {
    @include make-col(4);
  }
  .page-footer__col-right {
    @include make-col(5);
  }
}
@include media-breakpoint-down(sm) {

  .page-footer__col-left {
    @include make-col(12);
    margin-bottom: rem(30);
  }
  .page-footer__col-center {
    @include make-col(6);
    .page-footer__column {
      border: none;
      padding-left: 0;
    }
  }
  .page-footer__col-right {
    @include make-col(6);
  }
  .page-footer__column {
    margin-bottom: rem(30);
  }
  .page-footer__autor {
    text-align: left;
    font-size: 12px;
    margin-top: 15px;
  }
  .page-footer__top {
    display: none;
  }
}
@include media-breakpoint-down(xs) {
  .page-footer__col-center {
    @include make-col(12);

  }
  .page-footer__col-right {
    @include make-col(12);
  }
  .page-footer__column {
    border: none;
    padding-left: 0;
  }
}
