.b-advantages {
  background: #1b74c6;
  background-image: url(../img/general/preim-bottom.png);
  background-size: 100% 100%;
  padding: rem(27) 0 rem(30);
  position: relative;
  &:after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    height: rem(145);
    background: url(../img/general/about.png);
    background-size: 100% 100%;
    background-position: bottom right;
    pointer-events: none;
    @include media-breakpoint-down(md) {
      height: rem(50);
    }
  }
}
.b-advantages__items {

  text-align: center;
}
.b-advantages__item {
  display: inline-block;
  vertical-align: top;
  margin-bottom: rem(58);
  width: 218px;
}
.b-advantages__ico {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: rem(63);
  height: rem(63);
  border: rem(2) solid #7cb355;
  border-radius: 50%;
  margin-bottom: rem(16);
}
.b-advantages__text {
  font-family: $FiraSans;
  color: #ffffff;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
}
@include media-breakpoint-down(xs) {
  .b-advantages__items {
    display: flex;
    flex-wrap: wrap;
    margin: 0 10px;
  }
  .b-advantages__item {
    flex: 0 0 50%;
    max-width: 50%;
    padding: 0 10px;
    margin-bottom: rem(30);
  }
  .b-advantages__text {
    font-size: 12px;
    font-weight: 400;
  }
  .b-advantages__ico {
    img {
      max-width: 60%;
      max-height: 60%;
    }
  }

}
