$gamburger-width: 22px;
$gamburger-height: 14px;
$gamburger-line-height: 2px;
$gamburger-color:  #7cb355;
$gamburger-transition: 0.4s;

.gamburger{
  font-size: 10px;
  display: inline-block;
  width: $gamburger-width;
  height: $gamburger-height;
  transition: 1s;
  position: relative;
  cursor: pointer;

  @include media-breakpoint-down(sm) {
    font-size: 7px;
  }
  &.open {
    //overflow: hidden;
  }
}

.gamburger span{
  display: block;
  width: 100%;
  height: $gamburger-line-height;
  background: $gamburger-color;
  position: absolute;
}
.gamburger-line1{
  top: 0;
  left: 0;
  transition: $gamburger-transition;
}
.gamburger-line2{
  top: 50%;
  margin-top: - $gamburger-line-height / 2;
  left: 0;
  transform-origin: right;
  transform: scale(1.2, 1);
  background-color: #F78F16 !important;
}
.gamburger-line3{
  top: 100%;
  margin-top: - $gamburger-line-height;
  left: 0;
  transition: $gamburger-transition;
}
.gamburger.open .gamburger-line1{
  transform: rotate(45deg);
  top: 50%;
  margin-top: - $gamburger-line-height / 2;
}
.gamburger.open .gamburger-line2{
  position: absolute;
  right: -400vw;
  left: auto;
}

.gamburger.open .gamburger-line3{
  transform: rotate(-45deg);
  top: 50%;
  margin-top: - $gamburger-line-height / 2;
}
.gamburger.open span {
  background: #fff;
}
