.b-form-section {

}
.b-form-section__form-blue {
  max-width: 956px;
  padding: 130px 170px 107px;
  background: url(../img/general/blue-form.png);
  background-size: 100% 100%;

  @include media-breakpoint-up(lg) {
    margin-top: -210px;
    margin-left: -70px;
  }

}
.b-form-section__form-white {
  max-width: 849px;
  margin-left: auto;
  padding: 144px 130px 107px;
  background: url(../img/general/white-form.png);
  background-size: 100% 100%;
  @include media-breakpoint-up(lg) {
    margin-top: rem(-216);
  }
}
@include media-breakpoint-down(md) {
  .b-form-section__form-blue {
    padding: 130px 90px 107px;
  }
  .b-form-section__form-white {
    padding: 130px 90px 107px;
  }
}
@include media-breakpoint-down(xs) {
  .b-form-section {
    padding: rem(50) 0;
  }
  .b-form-section__form-blue {
    background: #1b5baf;
    border-radius: rem(20);
    padding: 20px;
    margin-bottom: rem(50);
  }
  .b-form-section__form-white {
    background: #fff;
    border-radius: rem(20);
    padding: 20px;
    box-shadow: 0 0 25px 0 #f2f5f8;
  }
}
