.b-about-slider {
  @include swiper-btn;
  position: relative;
  &:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: rem(144);
    background: url(../img/general/spa-before.png);
    background-size: 100% 100%;
    z-index: 20;
    @include media-breakpoint-down(md) {
      height: rem(50);
    }
  }
  &:after {
    content: '';
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: rem(57);
    background: url(../img/general/rest-after.png);
    background-size: 100% 100%;
    z-index: 10;
    @include media-breakpoint-down(md) {
      height: rem(30);
    }
  }
}
.b-about-slider__slide {
  min-height: 119vh;
  padding-bottom: rem(215);
  background-size: cover;
  background-position: center center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
}
.b-about-slider__title {
  @include title;
  color: #fff;
  text-align: right;
}
.b-about-slider__text {
  font-family: $openSans;
  color: #ffffff;
  font-size: rem(24);
  font-weight: 600;
  line-height: 1.37;
  text-align: right;
  &>* {
    margin-bottom: rem(32);
  }

}
.b-about-slider__arrows {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100vw;
  height: 50px;
  z-index: 50;
  max-width: 1440px;
}
