.b-main {
  min-height: 100vh;
  -webkit-background-size: cover;
  background-size: cover;
  padding: rem(78) 0 0;
  position: relative;
  &:after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    height: rem(399);
    background: url(../img/general/preim-top.png);
    background-size: 100% 100%;
    background-position: bottom right;
    pointer-events: none;
  }
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: block;
    background-color: #000;
    opacity: 0.2;
  }

}
.b-main__title {
  font-family: $FiraSans;
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  color: #ffffff;
  font-size: rem(55);
  font-weight: 700;
  letter-spacing: 1px;
  line-height: 1.04;
  max-width: 830px;
  position: relative;
  z-index: 10;
}
.b-main__form {
  display: flex;
  justify-content: flex-end;
  margin-top: rem(23);
  margin-right: rem(130);

}
.b-main__btn {
  @include btn;
  font-size: 13px;
  font-weight: 900;
  letter-spacing: 1px;
  text-transform: uppercase;
  padding: rem(21) rem(42);
  margin-top: rem(30);
}
@include media-breakpoint-down(md) {
  .b-main__form {
    margin-right: 0;
    justify-content: flex-start;
    display: none;
  }
  .b-main {
    min-height: calc(100vh - 80px);
  }
}
@include media-breakpoint-down(xs) {
  .b-main:after {
    height: 50px;
  }
  .b-main__form {
    padding-bottom: rem(50);
  }
}
