.b-entertainment {
  padding: 0 0 rem(32);

  @include swiper-btn;
}
.b-entertainment__title {
  @include title;
  text-align: right;
  font-size: rem(65);
  margin-bottom: rem(23);
}
.b-entertainment__text {
  font-family: $openSans;
  color: #3c3c3c;
  font-size: rem(18);
  font-weight: 600;
  line-height: 1.17;
  text-align: right;
  max-width: 932px;
  margin-left: auto;
  margin-bottom: rem(100);
}
.b-entertainment__group {
  margin-bottom: rem(85);
  @include media-breakpoint-up(xl) {
    margin-right: -40px;
  }
  &_right {
    @include media-breakpoint-up(xl) {
      margin-left: -40px;
      margin-right: 0;
    }
    .b-entertainment__body {
      border-radius:  rem(140) 0 0 rem(140);
      text-align: right;
    }
    .b-entertainment__icon-item {
      margin-right: 0;
      margin-left: rem(21);
    }
  }

}
.b-entertainment__row {
  display: flex;
  flex-wrap: wrap;
}
.b-entertainment__slider-col {
  flex: 0 0 58.2%;
  max-width: 58.2%;
}
.b-entertainment__body {
  background-color: #1a6fc3;
  color: #fff;
  border-radius: 0 rem(140) rem(140) 0;
  padding: rem(32) rem(44);
  height: 100%;
  display: flex;
  flex-direction: column;
}
.b-entertainment__slider {
  position: relative;
  .swiper-slide {
    width: 387px;
  }
}
.b-entertainment__link {
  display: block;
  @include zoom;
}
.b-entertainment__img {
  display: block;
  height: 100%;
  object-fit: cover;
}
.b-entertainment__text-col {
  flex: 0 0 41.8%;
  max-width: 41.8%;
}
.b-entertainment__group-name {
  font-family: $FiraSans;
  color: #ffffff;
  font-size: rem(36);
  font-weight: 800;
  margin-bottom: rem(19);
}
.b-entertainment__group-desc {
  // Style for " "
  font-family: $openSans;
  color: #ffffff;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.31;
  margin-bottom: 15px;
}
.b-entertainment__icons {
  margin-top: auto;
}
.b-entertainment__icon-item {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  vertical-align: top;
  width: rem(60);
  height: rem(60);
  border: rem(2) solid #ffffff;
  margin-right: rem(21);
  border-radius: 50%;
  img {
    max-width: 60%;
    max-height: 60%;
  }
}
@include media-breakpoint-down(sm) {
  .b-entertainment__slider-col {
    flex: 0 0 100%;
    max-width: 100%;
    order: -1;
  }
  .b-entertainment__text-col {
    flex: 0 0 100%;
    max-width: 100%;
  }
  .b-entertainment__group {
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
  .b-entertainment__body {
    border-radius: 0 0 10px 10px !important;
  }
}
