.b-sport-slider {
  position: relative;

  &:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: rem(144);
    background: url(../img/general/spa-before.png);
    background-size: 100% 100%;
    z-index: 20;
    @include media-breakpoint-down(md) {
      height: rem(50);
    }
  }
  @include swiper-btn;
  &:after {
    content: '';
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: rem(57);
    background: url(../img/general/water-after.png);
    background-size: 100% 100%;
    z-index: 10;
    @include media-breakpoint-down(md) {
      height: rem(30);
    }
  }
  .swiper-container {
    z-index: 11;
  }
  .swiper-button-prev, .swiper-button-next {
    z-index: 20;
  }
}
.b-sport-slider__slider {
  position: relative;
  z-index: 10;

}
.b-sport-slider__title {

  font-family: $FiraSans;
  color: #ffffff;
  font-size: rem(65);
  font-weight: 600;
  line-height: 1;
  text-align: right;
  z-index: 11 !important;
  position: absolute;
  top: rem(170);
  left: 0;
  right: 0;
  margin-bottom: -50px;
}
.b-sport-slider__slide {
  padding: 383px 0 147px;
  -webkit-background-size: cover;
  background-size: cover;
  background-position: center center;
  .container {
    max-width: 975px;
    position: relative;
    z-index: 10;
  }

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: block;
    background-color: #000;
    opacity: 0.2;
    z-index: 5;
  }

}
.b-sport-slider__name {
  font-family: $FiraSans;
  color: #ffffff;
  font-size: rem(55);
  font-weight: 800;
  line-height: 1.04;
  margin-bottom: rem(23);
}
.b-sport-slider__desc {
  font-family: $MPLUS1p;
  color: #ffffff;
  font-size: rem(24);
  font-weight: 800;
  line-height: 1.38;
  margin-bottom: rem(67);
}
.b-sport-slider__type {
  font-family: $openSans;
  color: #ffffff;
  font-size: rem(19);
  font-weight: 600;
  line-height: 1.37;
  margin-bottom: rem(20);
}
.b-sport-slider__list {
  //max-width: 750px;
}
.b-sport-slider__item {
  display: flex;
  align-items: center;
  margin-bottom: rem(35);
}
.b-sport-slider__ico {
  border: rem(2) solid #ffffff;
  width: rem(56);
  height: rem(56);
  border-radius: 50%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  margin-right: rem(18);
}
.b-sport-slider__item-name {
  font-family: $FiraSans;
  color: #ffffff;
  font-size: rem(20);
  font-weight: 700;
  line-height: 1.15;
  text-transform: uppercase;
}
.b-sport-slider__arrows {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100vw;
  height: 50px;
  z-index: 50;
  max-width: 1440px;
}
@include media-breakpoint-down(sm) {
  .b-sport-slider__title {
    text-align: left;
  }
}
@include media-breakpoint-down(xs) {
  .b-sport-slider__title {
    font-size: rem(43);
    top: rem(100);
  }
  .b-sport-slider__slide {
    padding-top: rem(210);
    padding-bottom: rem(80);
  }
  .b-sport-slider__name {
    font-size: rem(33);
    font-weight: 400;
  }
  .b-sport-slider__desc {
    font-weight: 400;
  }
}
