.b-gallery-slider {
  padding: rem(57) 0;
  @include swiper-btn;
  @include swiper-btn-green;
  @include swiper-static-arr;
}
.b-gallery-slider__title {
  @include title;
  font-size: rem(65);
  margin-bottom: rem(46);
}
.b-gallery-slider__slider {

}
.b-gallery-slider__slide {
  .row {
    &>* {
      &:nth-child(1),
      &:nth-child(2) {
        margin-bottom: 30px;
      }
    }
  }
}
.b-gallery-slider__item {
  display: block;
  position: relative;
  border-radius: 7px;
  overflow: hidden;
  padding: rem(18) rem(24);
  background-color: #000000;
  height: 100%;
  min-height: 170px;
  &:hover {
    .b-gallery-slider__bg {
      transform: scale(1.1);
    }
  }
}
.b-gallery-slider__bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-background-size: cover;
  background-size: cover;
  display: block;
  z-index: 5;
  opacity: 0.7;
  transform: scale(1);
  transition: 0.8s transform;

}
.b-gallery-slider__body {
  position: relative;
  z-index: 10;
  display: block;
}
.b-gallery-slider__top {
  display: flex;
  align-items: center;
  font-family: $Montserrat;
  color: #ffffff;
  font-size: 17px;
  font-weight: 500;
  line-height: 1.18;
  margin-bottom: rem(18);
  svg {
    flex-shrink: 0;
    margin-right: 14px;
  }
}
.icon__video {

}
.b-gallery-slider__item-name {
  font-family: $FiraSans;
  color: #ffffff;
  font-size: rem(22);
  font-weight: 700;
  line-height: 1.09;
  margin-bottom: rem(13);
  display: block;
}
.b-gallery-slider__date {
  padding-top: rem(9);
  border-top: 1px solid #fff;
  font-family: $openSans;
  color: #ffffff;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.15;
  display: flex;
  align-items: center;
  .icon__date {
    flex-shrink: 0;
    margin-right: rem(12);
    fill: #fff;
  }
}

.b-gallery-slider__item_big {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  min-height: 370px;
  padding: rem(32) rem(40);
  .b-gallery-slider__item-name {
    font-size: rem(38);
    line-height: 1.19;
    margin-bottom: rem(14);
    display: block;
  }
  .b-gallery-slider__date {
    padding-top: rem(19);
    .icon__date {
      margin-right: rem(11);
    }
  }
  .b-gallery-slider__top {
    margin-bottom: rem(14);
  }
}
.b-gallery-slider__arrows {
  text-align: right;
  margin-top: rem(11);
  @include media-breakpoint-down(sm) {
    display: flex;
    justify-content: space-between;
    margin: 0 0 rem(30);
  }
}
@include media-breakpoint-down(sm) {
  .b-gallery-slider__slide {
    .row {
      margin: 0 -7px;
      &>* {
        padding: 0 7px;
      }
    }
  }
  .b-gallery-slider__top {
    svg {
      max-width: 20px;
      max-height: 20px;
    }
  }
  .b-gallery-slider__date {
    font-size: 12px;
  }
  .b-gallery-slider__item-name {
    //font-weight: 400;
  }
}
