.b-event-slider {
  padding: rem(33) 0;
  //background: rgba(1, 56, 120, 0.14);
  .swiper-wrapper{
    display: flex;
  }
  @include swiper-btn;
  @include swiper-btn-green;
  @include swiper-static-arr;
}
.b-event-slider__title {
  @include title;
  font-size: rem(65);
  margin-bottom: rem(37);
}
.b-event-slider__slider {
  position: relative;
}
.b-event-slider__slide {

}
.b-event-slider__card {
  display: flex;
  flex-direction: column;
  height: 100%;
  border-radius: 7px;
  overflow: hidden;
  //box-shadow: 0 0 40px rgba(1, 56, 120, 0.14);
  background: #fff;
  max-width: 360px;
  margin-left: auto;
  margin-right: auto;
  &:hover {
    .b-event-slider__name {
      color: #7cb355;
    }
    .b-event-slider__img {
      transform: scale(1.1);
    }
  }
}

.b-event-slider__img-cont {
  overflow: hidden;
}
.b-event-slider__img {
  display: block;
  transform: scale(1);
  transition: 0.8s transform;
}
.b-event-slider__body {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: rem(24) rem(26);
  border: rem(2) solid rgba(1, 56, 120, 0.14);
  border-top: none;
  border-radius: 0 0 7px 7px;
}
.b-event-slider__name {
  font-family: $FiraSans;
  color: #3c3c3c;
  font-size:rem(28);
  font-weight: 600;
  line-height: 1.04;
  margin-bottom: rem(32);
  transition: 0.2s;
}
.b-event-slider__date {
  margin-top: auto;
  font-family: $openSans;
  color: #9e9e9e;
  font-size: 13px;
  font-weight: 400;
  line-height: 1;
  display: flex;
  align-items: center;
  .icon__date {
    flex-shrink: 0;
    margin-right: 6px;
    fill:  #7cb355;
  }

}
.b-event-slider__arrows {
  text-align: right;
  margin-top: rem(11);
  @include media-breakpoint-down(sm) {
    display: flex;
    justify-content: space-between;
    margin: 0 0 rem(30);
  }
}
