@mixin swiper-bullet {
  .swiper-pagination {
    position: static;
    display: flex;
  }
  .swiper-pagination-bullet {
    width: rem(140);
    height: auto;
    background: transparent;
    border-radius: 0px;
    text-align: left;
    opacity: 1;
    &:not(:last-child) {
      margin-right: rem(83);
    }
  }
  .swiper-pagination-bullet {

  }
  .swiper-pagination-bullet-active {

  }
  .swiper-pagination__num {
    display: block;
    font-family: $exo2;
    color: #d13f4e;
    font-size: rem(18);
    font-weight: 700;
    letter-spacing: 0.36px;
    margin-bottom: 10px;
  }
  .swiper-pagination__line {
    width: 90%;
    display: block;
    background-color: #444444;
    height: 2px;
    margin-bottom: 10px;
  }
  .swiper-pagination__label {
    display: block;
    font-family: $exo2;
    color: #444444;
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0.32px;
    line-height: 1;
  }
  .swiper-notification {

  }

}
@mixin swiper-btn {
  .swiper-button-next,
  .swiper-button-prev {
    width: rem(43);
    height: rem(43);
    border-radius: 50%;
    background: transparent;
    align-items: center;
    justify-content: center;
    fill: transparent;
    //position: static;
    display: inline-flex;
    //margin-top: 0;
    stroke: #fff;
    stroke-width: 4px;
    stroke-linecap: round;
    @include media-breakpoint-down(sm) {
      stroke: #7cb355 !important;
      stroke-width: 4px;
      stroke-linecap: round;
    }
  }
  .swiper-button-prev {
    svg {
      transform: scale(-1, 1);
    }
  }
  .swiper-button-disabled {
    stroke: #fff;
    stroke-width: 4px;
    stroke-linecap: round;
  }
}
@mixin swiper-btn-green {
  .swiper-button-next,
  .swiper-button-prev {
    stroke: #7cb355;
    stroke-width: 4px;
    stroke-linecap: round;
  }
}
@mixin swiper-fraction-pagination {
  .swiper-pagination {
    position: static;
    width: auto;
  }
}
@mixin swiper-static-arr {
  .swiper-button-next,
  .swiper-button-prev {
    position: static;
    margin: 0;
  }
}
@mixin tabs {
  .nav-link {
    font-family: $exo2;
    color: #000000;
    font-size: rem(28);
    font-weight: 400;
    letter-spacing: 0.56px;
    line-height: 1.04;
    border: none;
    border-bottom: rem(3) solid transparent;
    padding: 8px 0;
    margin-right: rem(67);
    &:hover {
      border: none;
      border-bottom: rem(3) solid  #d13f4e;
    }
  }
  .nav-link.active {
    background: transparent;
    border: none;
    color: #000000;
    border-bottom: rem(3) solid  #d13f4e;
    border-radius: 0px;
  }
  .nav-tabs {
    border: none;
    margin: 0;
  }
}
