// Mixins.

@mixin nl {
    margin: 0;
    padding: 0;
    text-indent: 0;
    list-style: none;
    list-style-position: outside;
}

@mixin cf {
    &:before,
    &:after {
        display: table;
        content: "";
    }

    &:after {
        clear: both;
    }

    .lt-ie8 & {
        zoom: 1;
    }
}

@mixin blocks-justify {
    text-align: justify;
    text-justify: newspaper;
}

// You can add your own mixins here:

$baseFontSize : 16;
@function rem($pixels, $context: $baseFontSize) {
    @if (unitless($pixels)) {
        $pixels: $pixels * 1px;
    }

    @if (unitless($context)) {
        $context: $context * 1px;
    }

    @return $pixels / $context * 1rem;
}
@mixin btn {
    display: inline-block;
    line-height: 1;

    font-family: $exo2;
    color: #ffffff;
    font-size: 13px;
    font-weight: 900;
    letter-spacing: 1px;

    border-radius: rem(25);
    background-color: #7cb355;

    padding: rem(10) rem(22);
    text-align: center;
    transition: 0.2s background, 0.2s color, 0.2s border;
    position: relative;

    &:hover {
        background-color:  complement(#7cb355);
        color: #fff;
    }
}
@mixin btn-light {
  background-color: #e8f1f5;
  color: #3a3a3a;
    &:hover {
        background-color:  #f78f16;
        color: #fff;
    }
}
@mixin btn-dark {
    background-color: #444444;
    color: #fff;
}
@mixin btn-yel {
    background-color: #f78f16;
    color: #fff;
    font-size: 13px;
    font-weight: 900;
    letter-spacing: 1px;
    text-transform: uppercase;
    padding: rem(20) rem(46);
    &:hover {
        background-color:  #7cb355;
        color: #fff;
    }
}
@mixin title {
    font-family: $FiraSans;
    color: #1a6fc3;
    font-size: rem(72);
    font-weight: 700;
    margin-bottom: rem(30);
    @include media-breakpoint-down(sm) {
        font-size: rem(42) !important;
    }
}
@mixin zoom {
    @include media-breakpoint-up(lg) {
        $galleryTransition: 0.3s;
        position: relative;
        &:before {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            background: rgba(#000000, 0.3);
            z-index: 5;
            opacity: 0;
            transition: $galleryTransition;
        }
        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            background: url(../img/general/zoom.svg);
            background-position: center center;
            background-repeat: no-repeat;
            background-size: rem(46) rem(47);
            z-index: 10;
            display: none;
            animation: top 0.3s forwards;

        }
        &:hover {
            &:before,
            &:after {
                opacity: 1;
                display: block;
            }
        }
    }
}
@mixin link-h {
    &:hover {
        color: #7cb355;
    }
}
