.b-rest-slider {
  @include swiper-btn;
  position: relative;
  &:after {
    content: '';
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: rem(153);
    background: url(../img/general/rest-after.png);
    background-size: 100% 100% !important;
    z-index: 10;
    @include media-breakpoint-down(md) {
      height: rem(50);
    }
  }
}

.b-rest-slider__slide {
  padding: rem(212) 0 rem(50);
  min-height: rem(1067);
  -webkit-background-size: cover;
  background-size: cover;
  background-position: center center;
}
.b-rest-slider__title {
  font-family: $FiraSans;
  color: #ffffff;
  font-size: rem(65);
  font-weight: 600;
  line-height: 1;
  margin-bottom: rem(30);
}
.b-rest-slider__text {
  font-family: $openSans;
  color: #ffffff;
  font-size: rem(24);
  font-weight: 600;
  line-height: 1.17;
  max-width: 835px;
  margin-bottom: rem(40);
}
.b-rest-slider__name {
  font-family: $FiraSans;
  color: #ffffff;
  font-size: rem(55);
  font-weight: 800;
  line-height: 1.16;
  margin-bottom: rem(15);
}
.b-rest-slider__places {
  font-family: $FiraSans;
  color: #ffffff;
  font-size: rem(24);
  font-weight: 600;
  line-height: 1.38;
  border-bottom: 1px solid #fff;
  padding-bottom: rem(24);
  max-width: 630px;
  margin-bottom: rem(22);
}
.b-rest-slider__desc {
  font-family: $openSans;
  color: #ffffff;
  font-size: rem(20);
  font-weight: 600;
  line-height: 1.6;
  max-width: 530px;
}
.b-rest-slider__bottom {
  text-align: right;
}
.b-rest-slider__round {
  text-align: left;
  background: url(../img/general/rest-r.png);
  background-size: 100% 100%;
  display: inline-block;
  padding: rem(100) rem(160);
  position: relative;
  @include media-breakpoint-up(xl) {
    margin-right: -80px;
    margin-top: -20px;
  }

}
.b-rest-slider__round-title {
  font-family: $FiraSans;
  color: #1a6fc3;
  font-size: rem(28);
  font-weight: 600;
  line-height: 1.39;
  margin-bottom: rem(18);
}
.b-rest-slider__list {
  margin: 0;
  padding: 0;
  list-style: none;

  li {
    font-family: $openSans;
    color: #3f3f3f;
    font-size: 16px;
    font-weight: 600;
    position: relative;
    padding-left: rem(35);
    margin-bottom: rem(16);
    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: -4px;
      width: rem(24);
      height: rem(17);
      background: url(../img/general/ok.svg);
      background-size: 100% 100%;
    }
  }
}

.b-rest-slider__cook {
  position: absolute;
  top: 0;
  right: 100%;
  margin-right: rem(-190);
}
.b-rest-slider__arrows {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100vw;
  height: 50px;
  z-index: 50;
  max-width: 1440px;
}
@include media-breakpoint-down(md) {
  .b-rest-slider__slide {
    padding-bottom: rem(30);
  }
  .b-rest-slider__slide {
    min-height: rem(830);
  }
}
@include media-breakpoint-down(sm) {
  .b-rest-slider__list li:before {
    top: 0.2em;
  }
  .b-rest-slider__round {
    background: #fff;
    padding: 20px;
    border-radius: 20px;
    margin: rem(50) 0;
  }
}
