.form-group {
  position: relative;
}

label.form-group {
  padding: 0 0 0;
  display: block;
}

label.form-group:after, label.form-group:before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #e2e2e2;
  bottom: 0;
  left: 0;
  transition: all 0.5s;
}

label.form-group:after {
  background-color: #7cb355;
  transform: scaleX(0);
}

label.form-group.is-active:after {
  transform: scaleX(1);
}

