  z-index: 10;
}

.form-ico svg {
  fill: #747474;
}

.form-label {
  display: block;
  transition: all 0.5s;
  font-family: "Open Sans", Arial, sans-serif;
  color: #656565;
  font-size: 15px;
  font-weight: 400;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  will-change: font-size, transform;
  padding: 8px 0;
  padding-left: 33px;
}

.form-control {
  background: transparent;
  width: 100%;
  border: none;
  outline: none;
  padding: 8px 0;
  padding-left: 33px;
  font-size: 16px;
  display: block;
  resize: none;
}

