  min-height: 34px;
}

.label--radio {
  display: block;
  user-select: none;
}

.label--radio input {
  display: none;
}

.label--radio > span {
  padding-left: 40px;
  position: relative;
  display: inline-block;
}

.label--radio > span:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0px;
  transform: translateY(-50%);
  display: inline-block;
  border-radius: 50%;
  border: 1px solid #747474;
  background-color: #fff;
  width: 1.3125rem;
  height: 1.3125rem;
  transition: 0.3s;
}

.label--radio > span:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  display: inline-block;
  border-radius: 50%;
  border: 1px solid #747474;
  background-color: #fff;
  width: 1.3125rem;
  height: 1.3125rem;
  opacity: 0;
  transition: 0.3s;
}

.label--radio .radio:checked + span:before {
  content: '';
}

.label--radio .radio:checked + span:after {
  content: '';
  background-color: #f78f16;
  border-color: transparent;
  transform: translateY(-50%) scale(0.6);
  opacity: 1;
}

.label--radio .radio:disabled + span {
  opacity: 0.5;
}

