.checkbox {
  font-family: "Open Sans", Arial, sans-serif;
  color: #6d6862;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.08;
}

.checkbox a {
  color: #7cb355;
  margin-left: 4px;
}

.checkbox input {
  display: none;
}

.checkbox > span {
  padding-left: 1.375rem;
  position: relative;
  display: inline-block;
}

.checkbox > span:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0px;
  transform: translateY(-50%);
  display: inline-block;
  border: 1px solid #a6a6a6;
  background-color: #ffffff;
  width: 1.0625rem;
  height: 1.0625rem;
  transition: 0.3s;
}

.checkbox > span:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  display: inline-block;
  background-color: #ececec;
  width: 1.0625rem;
  height: 1.0625rem;
  opacity: 0;
  transition: 0.3s;
}

.checkbox input:checked + span:before {
  content: '';
}

.checkbox input:checked + span:after {
  content: '';
  background-color: #7cb355;
  transform: translateY(-50%) scale(0.6);
  opacity: 1;
}

