.user-content q::before {
  display: none;
}

.user-content q::after {
  display: none;
}

.user-content q, .user-content blockquote {
  display: block;
  max-width: 975px;
  border-left: 0.1875rem solid #7cb355;
  padding: 0.875rem 1.25rem;
  margin-left: 1.875rem;
}

.user-content table {
  border-collapse: collapse;
  display: block;
  max-width: 100%;
  overflow-x: auto;
}

.user-content table th, .user-content table td {
  font-size: 14px;
  border: 1px solid #c4c4c4;
  padding: 6px 8px;
}

@media (max-width: 767.98px) {
  .user-content table th, .user-content table td {
    padding: 3px;
    font-size: 12px;
  }
}

.user-content img[style*='float:left'] {
  margin-right: 25px;
}

.user-content img[style*='float:right'] {
  margin-left: 25px;
}

.user-content iframe {
  max-width: 100%;
}

.user-content img {
  margin-bottom: 25px;
  height: auto !important;
}

@media (max-width: 991.98px) {
  .user-content img {
    float: none !important;
    display: block;
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
}

.gamburger {
  font-size: 10px;
  display: inline-block;
  width: 22px;
  height: 14px;
  transition: 1s;
  position: relative;
  cursor: pointer;
}

@media (max-width: 767.98px) {
  .gamburger {
    font-size: 7px;
  }
}

.gamburger span {
  display: block;
  width: 100%;
  height: 2px;
  background: #7cb355;
  position: absolute;
}

.gamburger-line1 {
  top: 0;
  left: 0;
  transition: 0.4s;
}

.gamburger-line2 {
  top: 50%;
  margin-top: -1px;
  left: 0;
  transform-origin: right;
  transform: scale(1.2, 1);
  background-color: #F78F16 !important;
}

.gamburger-line3 {
  top: 100%;
  margin-top: -2px;
  left: 0;
  transition: 0.4s;
}

.gamburger.open .gamburger-line1 {
  transform: rotate(45deg);
  top: 50%;
  margin-top: -1px;
}

.gamburger.open .gamburger-line2 {
  position: absolute;
  right: -400vw;
  left: auto;
}

.gamburger.open .gamburger-line3 {
  transform: rotate(-45deg);
  top: 50%;
  margin-top: -1px;
}

.gamburger.open span {
  background: #fff;
}
