.b-gropup-slider {
  padding: rem(66) 0;
  @include swiper-btn;
  @include swiper-btn-green;

}
.b-gropup-slider__title {
  @include title;
  font-size: rem(65);
  margin-bottom: rem(61);
}
.b-gropup-slider__slider {
  position: relative;
  margin-bottom: rem(53);
}
.b-gropup-slider__link {
  @include zoom;
  display: block;
  border-radius: 3px;
  overflow: hidden;
}
.b-gropup-slider__img {
  border-radius: 3px;
  display: block;
}
.b-gropup-slider__text {
  // Style for " "
  font-family: $openSans;
  color: #424242;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.2;
  margin-bottom: rem(48);


}

.b-gropup-slider__btn {
  @include btn;
  @include btn-yel;

}
.b-gropup-slider__arrows {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100vw;
  height: 50px;
  z-index: 50;
  max-width: 1440px;
}
