.b-room-slider {
  padding: rem(49) 0 rem(11);
  @include swiper-btn;
}
.b-room-slider__title {
  @include title;
  font-size: rem(65);
  margin-bottom: rem(56);
}
.b-room-slider__room-type {
  font-family: $FiraSans;
  color: #3a3a3a;
  font-size: rem(36);
  font-weight: 600;
  line-height: 1;
  margin-bottom: 30px;
}
.b-room-slider__slider {
  position: relative;
}
.b-room-slider__link {
  @include zoom;
  display: block;
  border-radius: 3px;
  overflow: hidden;
}
.b-room-slider__img {
  display: block;
}
.b-room-slider__bottom {
  margin-top: rem(50);
}
.b-room-slider__column-title {
  font-family: $FiraSans;
  color: #3a3a3a;
  font-size: rem(20);
  font-weight: 500;
  line-height: 1;
  margin-bottom: rem(24);
}
.b-room-slider__item {
  font-family: $openSans;
  color: #656565;
  font-size: 15px;
  font-weight: 400;
  margin-bottom: 15px;
}
.b-room-slider__price {
  text-align: center;
  margin-bottom: rem(30);
}
.b-room-slider__price-label {
  display: inline-block;
  vertical-align: bottom;
  color: #4c4c4c;
  font-family: $FiraSans;
  font-size: 17px;
  font-weight: 500;
  line-height: 1.18;
}
.b-room-slider__price-num {
  display: inline-block;
  vertical-align: bottom;
  font-size: rem(33);
  font-weight: 500;
  line-height: 1;
  color: #7cb355;
}
.b-room-slider__price-val {
  display: inline-block;
  vertical-align: bottom;
  color: #7cb355;
  font-family: $FiraSans;
  font-size: rem(36);
  font-weight: 600;
  line-height: 1;
}
.b-room-slider__btn {
  @include btn;
  @include btn-yel;
}
.b-room-slider__price-cont {
  text-align: right;

}
.b-room-slider__price-body {
  display: inline-block;
}


@include media-breakpoint-down(md) {
  .b-room-slider__price-cont {
    margin-top: rem(30);
  }
}
@include media-breakpoint-down(sm) {
  .b-room-slider__slider {
    margin-bottom: rem(30);
  }
  .b-room-slider__price-cont {
    text-align: left;
  }
  .b-rest-slider__cook {
    display: none;
  }
}
