.b-services {
  .container {
    max-width: 970px;
  }
}
.b-services__title {
  @include title;
  font-size: rem(45);
  text-align: center;
  margin-bottom: rem(58);
}
.b-services__item {
  display: flex;
  align-items: center;
  margin-bottom: rem(50);
}
.b-services__ico {
  flex-shrink: 0;
  border: rem(2) solid #7cb355;
  width: rem(75);
  height: rem(75);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: rem(17);
  border-radius: 50%;
}
.b-services__text {
  font-family: $MPLUS1p;
  color: #4d4d4d;
  font-size: rem(20);
  font-weight: 800;
  text-transform: uppercase;
}

