.b-order-form {
  .form-group {
    margin-bottom: rem(36);
    &_small {
      max-width: 220px;
    }
  }
}
.b-order-form__title {
  font-family: $FiraSans;
  color: #3a3a3a;
  font-size: rem(29);
  font-weight: 600;
  letter-spacing: 1px;
  line-height: 1.17;
  margin-bottom: rem(39);
}

.b-order-form_white {
  .b-order-form__title {
    color: #ffffff;
  }
  .form-label {
    color: #fff;
  }
  .form-group:after {
    color: #fff;
  }
  .form-group:after {
    background-color: #7cb355;
  }
  .form-group:after, .form-group:before {
    //background-color: #7cb355;
  }
  svg {
    fill: #7cb355;
  }
  .form-control {
    color: #fff;
  }
}
.b-order-form__btn-green {
  @include btn;
  margin-top: rem(16);
  padding: rem(20) rem(70);
  font-size: 13px;
  font-weight: 900;
  text-transform: uppercase;
}
.b-order-form__btn-orange {
  @include btn;
  @include btn-yel;
  margin-top: rem(16);
  padding: rem(20) rem(70);
  font-size: 13px;
  font-weight: 900;
  text-transform: uppercase;
}
.b-order-form__column-title {
  font-family: $FiraSans;
  color: #3a3a3a;
  font-size: rem(20);
  font-weight: 500;
  line-height: 1;
  margin-bottom: rem(24);
}
.b-order-form__bottom {
  margin-top: rem(55);
}
.b-order-form__item {
  margin-bottom: rem(15);
}
.b-order-form__column {
  margin-bottom: rem(46);
}
.b-order-form__result {
  margin-bottom: rem(47);
}
.b-order-form__result-num {
  font-family: $MPLUS1p;
  color: #7cb355;
  font-size: rem(33);
  font-weight: 500;
  line-height: 1.18;
  display: inline-block;
  vertical-align: bottom;
}
.b-order-form__result-val {
  color: #7cb355;
  font-family: $FiraSans;
  font-size: rem(36);
  font-weight: 600;
  line-height: 1.17;
  display: inline-block;
  vertical-align: bottom;
}
.b-order-form__text {
  font-family: $FiraSans;
  color: #3a3a3a;
  font-size: rem(24);
  font-weight: 400;
  letter-spacing: 1px;
  line-height: 1.17;
}
