.b-spa-slider {
  @include swiper-btn;
  border-bottom: rem(8) solid #fff;
  overflow: hidden;
  @include media-breakpoint-up(lg) {
    border-bottom-left-radius: rem(520) rem(40);
    border-bottom-right-radius: rem(990) rem(261);
    margin: 0 rem(-150) 0;
  }
  position: relative;
  &:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: rem(144);
    background: url(../img/general/spa-before.png);
    background-size: 100% 100%;
    z-index: 20;
    @include media-breakpoint-down(md) {
      height: rem(50);
    }
  }

}
.b-spa-slider__slide {
  padding: rem(201) 0 50px;
  min-height: 108vh;
  -webkit-background-size: cover;
  background-size: cover;
  background-position: center center;
}
.b-spa-slider__title {
  font-family: $FiraSans;
  color: #ffffff;
  font-size: rem(65);
  font-weight: 600;
  line-height: 1;
  text-align: right;
  margin-bottom: rem(20);
}
.b-spa-slider__text {
  font-family: $openSans;
  color: #ffffff;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.17;
  text-align: right;
  max-width: 835px;
  margin-left: auto;
}
.b-spa-slider__arrows {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100vw;
  height: 50px;
  z-index: 50;
  max-width: 1440px;
}
@include media-breakpoint-down(xs) {
  .b-spa-slider__slide {
    max-width: 100vw;
  }
}
